import React, { useState, useEffect } from 'react';
import { StyledNav, Logo, NavList, VPrintIcon } from "./styled";

const Navigation = () => {
  // Pocztkowy stan menu mobilnego - zamknite
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [isMobileView, setIsMobileView] = useState(window.innerWidth <= 1199);

  // Funkcja do obsugi kliknicia na przycisk menu
  const toggleMenu = () => {
    setIsMenuOpen(prevState => !prevState); // Przeczamy stan na przeciwny (toggle)
  };

  // Funkcja do zamknicia menu
  const closeMenu = () => {
    setIsMenuOpen(false);
  };

  // Efekt do ledzenia zmiany szerokoci ekranu
  useEffect(() => {
    const handleResize = () => {
      setIsMobileView(window.innerWidth <= 1199);
      
      // Ustaw isMenuOpen na false, jeli szeroko ekranu przekracza 1199px i menu jest otwarte
      if (window.innerWidth > 1199 && isMenuOpen) {
        setIsMenuOpen(false);
      }
    };

    window.addEventListener('resize', handleResize); // Dodajemy nasuchiwanie na zmiany szerokoci ekranu
    handleResize(); // Wywoujemy raz na pocztku, aby ustawi stan na podstawie szerokoci ekranu

    return () => window.removeEventListener('resize', handleResize); // Czyszczenie nasuchiwania na zmiany szerokoci ekranu przy odmontowaniu komponentu
  }, [isMenuOpen]); // Dodaj isMenuOpen do zalenoci efektu, aby reagowa na jego zmian

  return (
    <StyledNav>
      <Logo to="/">
        <VPrintIcon />
      </Logo>
      {isMobileView ? (
        // Menu z rozwijan list na mniejszych ekranach
        <div>
          {/* Przycisk menu */}
          <button onClick={toggleMenu}>Menu</button>
          {/* Ustawienie pocztkowego stanu menu mobilnego na zamknite */}
          {isMenuOpen && (
            <div>
              <NavList>O nas</NavList>
              <NavList>Technologia</NavList>
              <NavList>Detale</NavList>
              <NavList>Kontakt</NavList>
            </div>
          )}
        </div>
      ) : (
        // Nawigacja na wikszych ekranach
        <>
          <NavList>O nas</NavList>
          <NavList>Technologia</NavList>
          <NavList>Detale</NavList>
          <NavList>Kontakt</NavList>
        </>
      )}
    </StyledNav>
  );
};

export default Navigation;
